.snake {
    &__title {
        color: $c-main;
        letter-spacing: 0.5rem;
        font-size: 4.2rem;
        margin-bottom: 3.6rem;
        line-height: 1;
        align-self: start;
        font-weight: 400;
        background: linear-gradient(90deg, #4e287a,#47439a,#345db8);
        opacity: 0.7;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        color: #000;
    }

    &__wrapper {
        margin: auto;
        display: flex;
        justify-content: center;
        flex-direction: column;
        max-width: 400px;
        padding: 3.5rem 0;
        min-height: 100vh;
    }

    &__board {
        box-shadow: 0.2rem 0.2rem 1.5rem 0 rgba(255, 255, 255, 0.676);
        border-radius: 0.7rem;
    }
}
